import React from 'react'


import {  CirclesWithBar } from 'react-loader-spinner'
export default function Loader() {
  return (
   
      <CirclesWithBar
  height="80"
  width="80"
  color="#4fa94d"
  ariaLabel="circles-loading"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
  />

  )
}
